.navbar{
    color: white;

}
.navbar_main{
    height: 150px;
}
.nav-item{
    padding: 0px 10px;
}
.nav-item h3{
    font-size: 14px;
    font-weight: 500;
    line-height: 19.6px; 
    cursor: pointer;

}

.nav__btn-right button{
        /* Font & Text */
        font-family: Gilroy, sans-serif;
        font-size: 14px;
        font-style: normal;
        font-variant: normal;
        font-weight: 500;
        letter-spacing: normal;
        line-height: 14px;
        text-decoration: none solid rgb(255, 255, 255);
        text-align: start;
        text-indent: 0px;
        text-transform: none;
        vertical-align: baseline;
        white-space: normal;
        word-spacing: 0px;
    
        /* Color & Background */
        background-attachment: scroll;
        background-color: rgba(0, 0, 0, 0);
        background-image: none;
        background-position: 0px 0px;
        background-repeat: repeat;
        color: rgb(255, 255, 255);
    
        /* Box */
        height: 43.3333px;
        width: 200px;
        border: 0.666667px solid rgb(134, 93, 207);
        border-top: 0.666667px solid rgb(134, 93, 207);
        border-right: 0.666667px solid rgb(134, 93, 207);
        border-bottom: 0.666667px solid rgb(134, 93, 207);
        border-left: 0.666667px solid rgb(134, 93, 207);
        margin: 0px;
        padding: 14px 15px;
        max-height: 1e+06px;
        min-height: auto;
        max-width: none;
        min-width: auto;
    
        /* Positioning */
        position: relative;
        top: 0px;
        bottom: 0px;
        right: 0px;
        left: 0px;
        float: none;
        display: flex;
        clear: none;
        z-index: 1;
    
        /* List */
        list-style-image: none;
        list-style-type: disc;
        list-style-position: outside;
    
        /* Table */
        border-collapse: separate;
        border-spacing: 0px 0px;
        caption-side: top;
        empty-cells: show;
        table-layout: auto;
    
        /* Miscellaneous */
        overflow: hidden;
        cursor: pointer;
        visibility: visible;
    
        /* Effects */
        transform: none;
        transition: all 0.25s ease 0s;
        outline: rgb(255, 255, 255) dashed 0px;
        outline-offset: 0px;
        box-sizing: border-box;
        resize: none;
        text-shadow: none;
        text-overflow: clip;
        word-wrap: normal;
        box-shadow: none;
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
        border-bottom-left-radius: 8px;
        border-bottom-right-radius: 8px;
	
}